}/* Menu styling - menu.css */
.menu li a,
.menu li a:hover {
    color: inherit;
    text-decoration: none
}

.menu li,
.menu ul,
.menu.menu {
    margin: 0;
    padding: 0;
    color: inherit;
    background: inherit;
}

.menu li {
    margin: 0 1em;
    list-style: none
}

body.fixed {
    border-top: 10vh solid;
}

.fixed header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    min-height: 10vh;
}

header:after {
    content: "";
    clear: both;
    display: block
}

.menu li,
.menu li a {
    display: inline-block
}

.menu .dropdown {
    background: inherit;
    position: relative
}

.menu .dropdown ul {
    white-space: normal;
    position: absolute;
    top: 100%;
    left: 0;
    background: inherit;
    border-bottom: 3px solid;
    padding: 20px 0
}

.menu li ul {
    display: none
}

.menu li:hover ul {
    display: inline;
    margin-left: -1.5em
}

.menu li li {
    float: none
}

.menu .dropdown li {
    white-space: nowrap
}

.menu .dropdown li a {
    min-width: 200px
}

.menu li li a {
    float: none;
    color: inherit;
    display: block;
    padding: .5em
}

#menu-icon,
.menu-icon-wrapper {
    display: none
}

#menu-icon span {
    border: 2px solid;
    width: 30px;
    margin-bottom: 5px;
    display: block;
    transition: all .1s
}

.logo,
.logo a,
.logo a:hover {
    color: inherit;
    text-decoration: none
}

header {
    position: relative
}

header .menu {
    top: 100%
}

@media(max-width:1100px) {
    .menu,
    .menu.show {
        transition: max-height .5s linear
    }
    .menu-icon-wrapper {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex
    }
    .menu {
        margin: 0
    }
    .menu.show {
        overflow-y: auto;
        max-height: 100vh
    }
    .menu li,
    nav,
    ul.menu {
        float: none
    }
    .menu li,
    .menu li ul,
    .menu li:hover ul {
        display: block;
        margin: 0 1em
    }
    .menu,
    .menu a,
    .menu li {
        width: 100%
    }
    .menu li li,
    .menu>li {
        margin: 0;
        padding: 0
    }
    .menu li a,
    .menu li li a {
        margin: 0 10px;
        padding: 10px
    }
    .menu {
        display: block;
        max-height: 0;
        padding: 10px;
        position: absolute;
        overflow: hidden;
        left: 0;
        right: 0;
        background: inherit;
        z-index: 1000
    }
    .menu .dropdown ul {
        position: relative;
        margin: 0 1em;
        padding: 0;
        border-bottom: 0;
        border-left: 3px solid
    }
    #menu-icon {
        display: inline-block;
        cursor: pointer;
        align-self: center;
        flex-grow: 0;
        padding: 10px;
        z-index: 4;
        height: 40px
    }
    #menu-icon.active .first {
        transform: rotate(45deg);
        margin-top: 10px
    }
    #menu-icon.active .second {
        transform: rotate(135deg);
        position: relative;
        top: -9px
    }
    #menu-icon.active .third {
        display: none
    }
    #menu-icon .third {
        margin-bottom: 0
    }
}
/* General styling - style.css */

*,
