    background: #255587;
    color: #bfbfbf
}

input[type=color],
input[type=date],
input[type=datetime-local],
input[type=datetime],
input[type=email],
input[type=file],
input[type=month],
input[type=number],
input[type=password],
input[type=phone],
input[type=range],
input[type=search],
input[type=tel],
input[type=text],
input[type=time],
input[type=url],
select,
textarea {
    border: 1px solid #ccc;
    margin-bottom: 1em;
    padding: .3em .5em;
    border-radius: .2em
}

input[type=checkbox],
input[type=radio] {
    float: left;
    line-height: 2em;
    margin-right: .5em;
    margin-top: .5em
}

.responsive-video {
    padding-top: 25px;
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    margin: 0 0 1em
}

.responsive-video iframe {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    max-width: 100%
}


